@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&family=Zeyada&display=swap";
* {
  box-sizing: border-box;
}

body {
  background: url("kalal1.ed76a7b4.jpg") center / cover no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
  overflow: hidden;
}

h1 {
  position: relative;
  top: 350px;
}

h4 {
  font-size: 14px;
  position: absolute;
  top: 400px;
}

#login-error-msg-holder {
  place-items: center;
  width: 400px;
  height: 50px;
  position: relative;
  top: 400px;
}

#login-error-msg {
  text-align: center;
  color: #0e0d0d;
  opacity: 0;
  background-color: #5c5b5b;
  border: 1px solid #7a7a7a;
  width: 400px;
  margin: auto;
  padding: 5px;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
}

.button-create, .button-login {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #131212;
  border-radius: 5px;
  width: 150px;
  height: 30px;
  margin: auto;
  padding-top: 4px;
  display: block;
  top: 50px;
  box-shadow: 0 25px 30px -5px #16181b;
}

.button:active {
  background-color: #4d504d;
  transform: translateY(4px);
  box-shadow: 0 5px #666;
}

#login-form {
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 10px;
  display: flex;
  position: relative;
  top: 75px;
}

.form-field {
  justify-items: auto;
  border: 0;
  border-radius: 3px;
  height: 25px;
  margin: 10px;
  padding: 10px;
  font-size: 15px;
  box-shadow: 0 25px 30px -5px #16181b;
}

.form-field:focus {
  outline: none;
}

@media (width <= 680px) {
  h1 {
    top: 200px;
  }

  h4, #login-error-msg-holder {
    top: 300px;
  }

  #login-form {
    height: auto;
    top: 0;
  }

  .button-create, .button-login {
    top: 0;
  }

  .form-field {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

